import React, { useState } from 'react'
import copy from 'clipboard-copy';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

export default function Text(props) {

  const [text, changetext] = useState('');
  const [restore, restoretext] = useState('');
  // let str;

    const onUpClickHandle = () => {
        // console.log('uppercase button clicked');
        let newvalue = text.toUpperCase();
        changetext(newvalue);
    }

    const onCopyHandle=()=>{
      copy(text);

      toast('copied');
    }

    const onfirstletterClickHandle = () => {
        // console.log('firstletter  button clicked');
        const newvalue = convertFirstLetterToUpperCase(text);
        changetext(newvalue);
    }

   const convertFirstLetterToUpperCase = (text) => {
    const lines = text.split('\n');
    const convertedLines = lines.map(line => {
      const trimmedLine = line.trim();
      if (trimmedLine.length === 0) {
        return line; // Preserve empty lines
      }
      const convertedLine = trimmedLine.charAt(0).toUpperCase() + trimmedLine.slice(1).toLowerCase();
      return line.replace(trimmedLine, convertedLine);
    });
    return convertedLines.join('\n');
  };


    const onLoClickHandle = () => {
        // console.log('lowercase button clicked');
        let newvalue = text.toLowerCase();
        changetext(newvalue);
    }

    

    const onChangeHandle = (event) => {
        // console.log('text has been changed');
        changetext(event.target.value)
    }

    const CapitalizecaseOnClick = () =>{
        const convertedText = convertFirstLetterToTitleCase(text);
        changetext(convertedText);
    }

    const RemoveText=()=>{
      restoretext(text);
      changetext('');
    }
    const RestoreText=()=>{
      changetext(restore);
    }

    const convertFirstLetterToTitleCase = (text) => {
        const words = text.split(' ');
        const convertedWords = words.map(word => {
          if (word.length === 0) {
            return word;
          }
          const convertedWord = word.charAt(0).toUpperCase() + word.slice(1).toLowerCase();
          return convertedWord;
        });
        return convertedWords.join(' ');
      };

    return (
        <>
        <div className="mb-3" style={{color:props.mode==='dark'?'white':'black'}}>
            <h1>{props.defaultText}</h1>
            <div className="mb-3">
                <textarea className="form-control" value={text} onChange={onChangeHandle} style={{backgroundColor: props.mode==='dark'?'grey':'white' , color:props.mode==='dark'?'white':'black'}} id="exampleFormControlTextarea1" rows="9"></textarea>
            </div>
            <button className="btn btn-primary mx-2" onClick={onUpClickHandle}>Convert to uppercase</button>
            <button className="btn btn-primary mx-2" onClick={onLoClickHandle}>Convert to lowercase</button>
            <button className="btn btn-primary mx-2" onClick={onfirstletterClickHandle}>Sentence Case</button>
            <button className="btn btn-primary mx-2" onClick={CapitalizecaseOnClick}>Capitalize Case</button>
            <button className="btn btn-primary mx-2" onClick={onCopyHandle}>Copy text</button>
            <button className="btn btn-primary mx-2" onClick={RemoveText}>Remove Text</button>
            <button className="btn btn-primary mx-2" onClick={RestoreText}>Restore Text</button>
            
        </div>
        <div className="mb-3"  style={{color :props.mode==='dark' ? 'white':'black'}}>
            <p>Number of words are {text.split(' ').length} and characters are {text.length}</p>
            <p>Preview</p>
            <p>{text.split('').length===0 ? `Enter the text to Preview here`:text}</p>
        </div>
        </>

    )
}